.ant-space-item h1{
	margin: 0
}

html {
	scroll-behavior: smooth;
  background: #0e1514
}
.mainBody {
	background: #0e1514
}
.mainHeader {
  position: sticky;
  background: hsla(274, 65%, 12%, 1);
  display: flex;
  justify-content: center; 
}
.headerLink{
  width: 70px;
  padding: 0;
  background: transparent;
  border: none;
  border-radius: 0;
  color: #fff;
  text-decoration: none;
  transition: 0.3s;
}
.headerLink:hover {
  color: white;
}
.headerLink span {
	color: white;
	text-decoration: none;
  	border-bottom: 1px solid #fff;
}:hover {
  color: white;
}


.header{
	gap: 20px;
	min-width: 300px;
  	width: 30vw;
	height: 55px;
  	max-width: 1200px;
  	margin: 0 auto;
	margin-top: 20px;
	box-shadow: 0 8px 32px 0 rgba(25, 28, 71, 0.37);
	backdrop-filter: blur( 16px );
	-webkit-backdrop-filter: blur( 16px );
	border-radius: 50px;
	border: 1px solid rgba( 255, 255, 255, 0.18 );
	justify-content: center;
	animation: header 10s ease;
}
@keyframes header {
	0% {transform: translateY(-100);}
	100% {transform: translateY(0);}
}

@keyframes wheel {
	to {
		opacity: 0;
		top: 27px;
	}
}

@-webkit-keyframes wheel {
	to {
		opacity: 0;
		top: 27px;
	}
}
.field {
	display: flex;
	align-items: center;
	justify-content: space-around;
	width: 100%;
    position: absolute;
    bottom: 20px;
}
.mouse {
    display: flex;
    justify-content: center;
	width: 20px;
	height: 35px;
	border: 3px solid #fff;
	border-radius: 60px;
	position: relative;
	&::before {
		content: '';
		width: 5px;
		height: 5px;
		position: absolute;
		top: 10px;
		left: 50%;
		transform: translateX(-50%);
		background-color: #fff;
		border-radius: 50%;
		opacity: 1;
		animation: wheel 2s infinite;
		-webkit-animation: wheel 2s infinite;
	}
}

.logo{
	width: 102px;
	height: 46px;
}